import { mapState } from "vuex";

export default {
    data() {
        return {
            mch_id: 0,
            order: {
                total_pay_price: '',
                express_price: '',
                total_goods_price: '',
                total_goods_original_price: ''
            },
            cancelRefund: false,
            addressId: '0',
            notRefund: false,
            isRefund: false,
            openAddress: false,
            isReason: false,
            beToSend: false,
            showForm: false,
            noAddress: false,
            active: null,
            show: false,
            start: [],
            end: [],
            search: false,
            keyword: '',
            list: [],
            candidate: [],
            date_start: '',
            date_end: '',
            time: 0,
            inSearch: false,
            address: [],
            today: '',
            yesterday: '',
            weekday: '',
            chooseTime: false,
            noRefund: false,
            cancelOrder: false,
            detail: {},
            changePrice: false,
            callPhone: false,
            custom: false,
            mobile: '',
            isSend: false,
            sendType: 0,
            price: 0,
            express: 0,
            total: 0,
            about: '',
            reset_time: 0,
            dd: 0,
            hh: 0,
            mm: 0,
            status: 1,
            first: false,
            refund_price: 0,
            iphone_x: false
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            mchImg: state => state.mallConfig.__wxapp_img.mch,
        })
    },
    methods: {
        toExpressInfo(item) {
            uni.navigateTo({
                url: '/pages/app_admin/express/express?id='+item.id+'&express='+item.detailExpress[0].express+'&express_no='+item.detailExpress[0].express_no+'&customer_name='+item.detailExpress[0].customer_name
            })
        },
        toExpressMore(item) {
            uni.navigateTo({
                url: '/pages/order/express-list/express-list?order_id='+item.id
            })
        },
        setSendType(e) {
            this.sendType = e;
        },
        imageFormLoad(idx,index) {
            this.order.detail[idx].form_data[index].loadOver = false;
        },
        toChangeAddress(item) {
            this.beToSend = false;
            let order_no = this.order.order_no ? this.order.order_no : this.detail.order_no
            uni.navigateTo({
                url: '/plugins/mch/mch/change-add/change-add?mch_id='+this.mch_id+'&order_no='+order_no
            })
        },
        copy() {
            this.$utils.uniCopy({
                data: this.status == 2 ? this.order.order.address:this.order.address,
                success() {



                },
            })
        },
        look(e) {
            uni.previewImage({
                current: e, // 当前显示图片的http链接
                urls: [e] // 需要预览的图片http链接列表
            })
        },
        agree() {
            let that = this;
            if (that.detail.refund_price > 0) {
                uni.showLoading({
                    title: '处理中...'
                });
                that.$request({
                    url: that.$api.mch.refund_handle,
                    data: {
                        order_refund_id: that.detail.id,
                        type: that.detail.type,
                        is_agree: 1,
                        mch_id: that.mch_id,
                        refund_price: that.refund_price,
                        merchant_remark: that.about
                    },
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code == 0) {
                        uni.showModal({
                            title: '提示',
                            content: response.msg,
                            showCancel: false,
                            success: function (res) {
                                if (res.confirm) {
                                    that.list = [];
                                    that.notRefund = false;
                                    that.openAddress = false;
                                    that.isRefund = false;
                                    that.addressId = 0;
                                    that.getList();
                                }
                            }
                        });
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            } else {
                uni.showToast({
                    title: '退款金额需大于零',
                    icon: 'none',
                    duration: 1000
                });
            }
        },
        getTime() {
            let reset_time = this.reset_time - 1;
            let dd = 0;
            let hh = parseInt(reset_time / 3600);
            if (reset_time > 86400) {
                dd = parseInt(reset_time / 86400);
                hh = parseInt((reset_time - 86400 * dd) / 3600);
            }
            let h = reset_time % 3600;
            let mm = parseInt(h / 60);
            if (hh < 10) {
                hh = '0' + hh.toString();
            }
            if (mm < 10) {
                mm = '0' + mm.toString();
            }
            this.reset_time = reset_time;
            this.dd = dd;
            this.hh = hh;
            this.mm = mm;
        },
        toSendType() {
            let that = this;
            if (that.sendType == 1) {
                if (that.status == 2) {
                    uni.showLoading({
                        title: '加载中...'
                    });
                    that.$request({
                        url: that.$api.mch.refund_handle,
                        data: {
                            is_express: 0,
                            merchant_remark: '',
                            type: that.detail.type,
                            mch_id: that.mch_id,
                            is_agree: 1,
                            order_refund_id: that.detail.id
                        },
                        method: 'post'
                    }).then(response => {
                        uni.hideLoading();
                        if (response.code == 0) {
                            uni.showToast({
                                title: response.msg,
                                type: 'success',
                                mask: false,
                                duration: 2000
                            });
                            that.isSend = false;
                            setTimeout(()=>{
                                that.list = [];
                                that.page = 1;
                                that.sendType = 0;
                                that.getList();
                            },1000)
                        } else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    }).catch(response => {
                        uni.hideLoading();
                        uni.showToast({
                            title: response,
                            icon: 'none',
                            duration: 1000
                        });
                    });
                } else {
                    uni.showLoading({
                        title: '加载中...'
                    });
                    let para = {
                        is_express: 2,
                        mch_id: that.mch_id,
                        words: '',
                        order_id: that.detail.id
                    }
                    if(that.detail.detailExpress.length > 0) {
                        para.express_id = that.detail.detailExpress[0].id
                    }
                    that.$request({
                        url: that.$api.mch.order_send,
                        data: para,
                        method: 'post'
                    }).then(response => {
                        uni.hideLoading();
                        if (response.code == 0) {
                            uni.showToast({
                                title: response.msg,
                                type: 'success',
                                mask: false,
                                duration: 2000
                            });
                            that.list = [];
                            that.page = 1;
                            that.isSend = false;
                            that.sendType = 0;
                            that.getList();
                        } else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    }).catch(response => {
                        uni.hideLoading();
                        uni.showToast({
                            title: response,
                            icon: 'none',
                            duration: 1000
                        });
                    });
                }
            } else {
                that.isSend = false;
                if (that.status == 2) {
                    uni.navigateTo({
                        url: '/plugins/mch/mch/send/send?order_refund_id=' + this.detail.id + '&mch_id=' + this.mch_id + '&is_send=' + that.detail.is_send
                    })
                } else {
                    uni.navigateTo({
                        url: '/plugins/mch/mch/send/send?id=' + this.detail.id + '&mch_id=' + this.mch_id + '&is_send=' + that.detail.is_send
                    })
                }
            }
        },
        toExpress(e,is_send) {
            let id = e.id;
            let order_refund_id = e.refund;
            if (id > 0) {
                uni.navigateTo({
                    url: '/plugins/mch/mch/send/send?id=' + id + '&is_send=' + is_send + '&mch_id=' + this.mch_id
                })
            } else if (order_refund_id) {
                uni.navigateTo({
                    url: '/plugins/mch/mch/send/send?order_refund_id=' + order_refund_id + '&is_send=' + is_send + '&mch_id=' + this.mch_id
                })
            }
        },
        getList() {
            let that = this;
            that.about = '';
            let url = that.$api.mch.order_detail;
            if(that.status == 2) {
                url = that.$api.mch.refund_detail;
            }
            that.$request({
                url: url,
                data: {
                    id: that.id,
                    mch_id: that.mch_id,
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.first = true;
                    let order = response.data.detail;
                    if(that.status == 2) {
                        that.address = response.data.address;
                    }else {
                        order.address = order.address.replace(/\s*/g, "");
                    }
                    let reset_time = 0;
                    if (order.auto_cancel > 0) {
                        reset_time = order.auto_cancel;
                    } else if (order.auto_confirm > 0) {
                        reset_time = order.auto_confirm;
                    } else if (order.auto_sales > 0) {
                        reset_time = order.auto_sales;
                    }
                    that.showForm = false;
                    if(order.detail.length > 0) {
                        for(let i in order.detail) {
                            if(order.detail[i].form_data != null && order.detail[i].form_data.length > 0) {
                                for(let item of order.detail[i].form_data) {
                                    if(item.value != null) {
                                        that.showForm = true;
                                    }
                                }
                            }
                        }
                    }
                    that.order = order;
                    that.reset_time = reset_time;
                    setInterval(function() {
                        that.getTime();
                    }, 1000)
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        toCall(e) {
            if(this.status == 1) {
                this.mobile = e.mobile;
            }else {
                this.mobile = e.order.mobile;
            }
            this.callPhone = !this.callPhone;
        },
        decline(type) {
            let that = this;
            if (type == 1) {
                if (that.addressId < 1) {
                    uni.showToast({
                        title: '请选择地址',
                        icon: 'none',
                        duration: 1000
                    });
                    return false;
                }
            }
            if (that.cancelRefund) {
                type = 2;
            }
            uni.showLoading({
                title: '处理中...'
            });
            that.$request({
                url: that.$api.mch.refund_handle,
                data: {
                    order_refund_id: that.detail.id,
                    type: that.detail.type,
                    mch_id: that.mch_id,
                    is_agree: type,
                    address_id: that.addressId,
                    refund_price: that.detail.refund_price,
                    merchant_remark: that.about
                },
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    uni.showModal({
                        title: '提示',
                        content: response.msg,
                        showCancel: false,
                        success: function (res) {
                            if (res.confirm) {
                                that.list = [];
                                that.notRefund = false;
                                that.cancelOrder = false;
                                that.openAddress = false;
                                that.addressId = 0;
                                that.getList();
                            }
                        }
                    });
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        call() {
            uni.makePhoneCall({
                phoneNumber: this.mobile
            })
            this.callPhone = !this.callPhone;
        },
        toRefund(e) {
            this.detail = e;
            this.refund_price = e.refund_price;
            this.isRefund = !this.isRefund;
        },
        // 取消
        cancel() {
            this.about = '';
            this.date_start = '';
            this.date_end = '';
            this.isRefund = false;
            this.beToSend = false;
            this.chooseTime = false;
            this.noRefund = false;
            this.changePrice = false;
            this.cancelOrder = false;
            this.callPhone = false;
            this.isSend = false;
            this.notRefund = false;
            this.openAddress = false;
            this.isReason = false;
            this.noAddress = false;
            this.addressId = 0;
        },
        noCancel() {
            let that = this;
            uni.showLoading({
                title: '处理中...'
            });
            that.$request({
                url: that.$api.mch.cancel,
                data: {
                    status: 2,
                    remark: that.about,
                    mch_id: that.mch_id,
                    order_id: that.detail.id
                },
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        type: 'success',
                        mask: false,
                        duration: 2000
                    });
                    that.noRefund = false;
                    that.getList();
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        cancelSubmit() {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            that.$request({
                url: that.$api.mch.cancel,
                data: {
                    status: 1,
                    mch_id: that.mch_id,
                    remark: '',
                    order_id: that.detail.id
                },
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    uni.showToast({
                        title: '取消成功',
                        duration: 2000,
                        type: 'success',
                        mask: false
                    });
                    that.list = [];
                    that.isRefund = false;
                    that.cancelOrder = false;
                    that.page = 1;
                    that.getList();
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        lookAbout(e) {
            this.detail = e;
            this.isReason = true;
        },
        chooseAddress(e) {
            if (this.addressId == e) {
                this.addressId = ''
            }else {
                this.addressId = e
            }
        },
        refundHandle(type,detail,cancel) {
            if (cancel == 1) {
                this.cancelRefund = true;
            }
            if (type == 1) {
                if (this.address.length == 0) {
                    this.noAddress = true;
                } else {
                    this.detail = detail;
                    this.openAddress = true;
                }
            } else if (type == 2) {
                this.detail = detail;
                this.notRefund = true;
            }
        },
        beNotRefund(e) {
            this.detail = e;
            this.noRefund = !this.noRefund;
        },
        toCancelorder(e) {
            this.detail = e;
            this.cancelOrder = !this.cancelOrder;
        },
        // 确认修改价格
        submitChange() {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            if (that.price > -0.01 && that.express > -0.01) {
                that.$request({
                    url: that.$api.mch.update_total_price,
                    data: {
                        order_id: that.detail.id,
                        mch_id: that.mch_id,
                        total_price: that.price,
                        express_price: that.express
                    },
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code == 0) {
                        uni.showToast({
                            title: response.msg,
                            duration: 2000,
                            type: 'success',
                            mask: false
                        });
                        that.changePrice = false;
                        that.getList();
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            } else {
                if (!that.price || typeof(that.price) != 'number') {
                    uni.showToast({
                        title: '商品总价必须大于等于0',
                        icon: 'none',
                        duration: 1000
                    });
                } else if (!that.express || typeof(that.express) != 'number') {
                    uni.showToast({
                        title: '运费必须大于等于0',
                        icon: 'none',
                        duration: 1000
                    });
                }
            }
        },
        toSend(e) {
            this.detail = e;
            if(e.send_type == 1 && !e.address) {
                this.beToSend = true;
            }else {
                this.isSend = true;
            }
        },
        toChange(e) {
            this.detail = e;
            this.changePrice = !this.changePrice;
            this.price = e.total_goods_price;
            this.express = e.express_price;
            this.total = '￥' + e.total_pay_price;
        },
        priceInput: function(e) {
            if (e.detail.value > -0.01) {
                this.total = '￥' + (+e.detail.value + +this.express).toFixed(2)
            } else {
                this.total = '数据有误'
            }
        },
        expressInput: function(e) {
            if (e.detail.value > -0.01) {
                this.total = '￥' + (+e.detail.value + +this.price).toFixed(2)
            } else {
                this.total = '数据有误'
            }
        },
        toAgreeCancel(e) {
            this.detail = e;
            this.isRefund = !this.isRefund;
        },
    },
    onShow: function() {
        if(this.first) {
            this.getList();
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
        that.id = options.id;
        that.status = options.status;
        that.mch_id = options.mch_id;
        that.getList();
    }
}
